
import PopupPicker from 'vux/src/components/popup-picker/index.vue'

import MenuHeadMaster from './home/headmaster.vue'
import MenuTeahcer from './home/teacher.vue'
import Recruit from './home/recruit.vue'
import common from '@/libs/common.mixin'
import globals from '@/libs/globals.mixin'

export default{
mixins: [ common,globals ],
components: {
Recruit,
PopupPicker,
MenuTeahcer,
MenuHeadMaster
},
data () {
return {
role: [1],
popupShow: false
}
},
methods: {
changeBranch () {
this.branch().popupShow = true
},
onChange (val) {
this.$store.commit('updateRoleId',val[0])
},
showPopup () {
this.popupShow = this.roleList.length>0
}
},
computed: {
roleId () {
let rid = this.$store.state.user.default_rid
if(this.rids.length>0&&this.rids.indexOf(parseInt(rid))===-1){
rid = this.rids[0]
}
this.role = [rid]

return rid
},
rids () {
let user = this.user$,

menus = [1,3,7],
rids = [],
result = []
if(user.employee){
rids = user.employee.rids
}
if(rids.length>0){
menus.forEach(m=>{
if(rids.indexOf(m)>-1){
result.push(m)
}
})
}
return result
},
roleList () {
let result = []
if(this.rids.length>0){
this.rids.forEach(r=>{
result.push({
name: this.$filter('role_name')(r),
value: r
})
})
}
return result
}
}
}



