
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'


export default{
components: {
Grid,
GridItem
},
computed: {
home_menu () {
let list = this.$store.state.ui_config.home_menu||[],
result = []
if(list.length > 0){
result = list.filter(m=>m.rids.includes(1)||m.rids.includes(4))
}
return result
}
}

}



